<template>
  <div id="playlist">
    <h1>Playlist</h1>
    <form action="" v-on:submit.prevent="addSong">
      <input type="text" name="url" />
    </form>
  </div>
</template>

<script>
export default {
  name: 'playlist',
  data() {
    return {
      loading: false,
      songs: {},
    };
  },
  created() {
    this.getSongs();
  },
  watch: {
    // call again the method if the route changes
    $route: 'getSongs',
  },
  methods: {
    getSongs() {
      this.$remote.$on('getSongs', (data) => {
        console.log(data);
      });
    },
    addSong(url) {
      console.log(url);
      this.$remote.$emit('addSong', url);
    },
  },
};
</script>

<style>
</style>



